  background-color: #ffffff;
  color: #3F8DA8;
  padding: 5px 10px;
  margin: 15px;
}

.dog-icon {
  width: 50px;
  margin: 10px;
}

.head-image {
  width: 300px;
  height: 300px;
  border: 3px solid #3F8DA8;
  border-radius: 50%;
}

.body-image {
  width: 250px;
  height: 250px;
  border: 3px solid #3F8DA8;
  border-radius: 50%;
}

.social-icon {
  padding: 15px;
